const TaskList = ({ placeId, placeName }) => {
  const [tasks, setTasks] = useState([]);
  const [question, setQuestion] = useState(5);

  const reportItemCounts = Array.from({ length: 10 }, (_, i) => `${i + 1}`);

  const loadTasks = async () => {
    if (!placeId) return;
    try {
      const res = await fetchTasks(placeId);
      setTasks(res.data);
    } catch (err) {
      console.error("", err);
    }
  };

  useEffect(() => {
    loadTasks();
  }, [placeId]);

  const handleAddTask = async (newTask) => {
    try {
      await addTask(placeId, newTask.text, 0, newTask.limit_time); //  number=0
      await loadTasks();
    } catch (err) {
      console.error("", err);
    }
  };

  const [editingIndex, setEditingIndex] = useState(null);

  const handleEditTask = async (index, text, limit_time) => {
    try {
      const updated = [...tasks];
      const task = updated[index];
      task.itemtext = text;
      task.limit_time = limit_time;
      setTasks(updated);
      await updateTask(task.id, text, limit_time); //  API
    } catch (err) {
      console.error("", err);
    }
  };
  

const handleDeleteTask = async (index) => {
  const confirmed = window.confirm("");
  if (!confirmed) return;

  const target = tasks[index];
  await deleteTask(target.id);
  const updated = tasks.filter((_, i) => i !== index);
  setTasks(updated);
};

  return (
    <div className="task-list-container">
      <Paper className="task-list-paper" elevation={3}>
        <Box className="task-list-header">
          <Box className="task-list-title-row">
            <Typography variant="h6" sx={{ fontWeight: "bold" }}>
              {placeName} 
            </Typography>
            <Typography variant="body2" color="text.secondary">
              {tasks.length}
            </Typography>
          </Box>

          <Box display="flex" gap={2}>
            <SelectBox
              label=""
              options={reportItemCounts}
              value={`${question}`}
              onChange={(val) => setQuestion(parseInt(val.replace("", "")))}
            />
          </Box>

          <TaskForm onAdd={handleAddTask} />
        </Box>

        <Paper className="task-list-scroll-container" variant="outlined">
          {/*  */}
          <Box
            className="task-list-table-header"
            display="flex"
            sx={{
              fontWeight: 'bold',
              backgroundColor: '#f5f5f5',
              px: 2,
              py: 1,
              borderBottom: '1px solid #ccc',
              position: 'sticky',
              top: 0,
              zIndex: 1
            }}
          >
            <Box sx={{ width: '50%' }}></Box>
            <Box sx={{ width: '25%' }}></Box>
            <Box sx={{ width: '25%' ,textAlign: 'right' }}></Box>
          </Box>

          {/*  */}
          <Box className="task-list-scroll-content">
            <List sx={{ minHeight: 80 }}>
              {tasks.map((task, index) => (
                <div key={task.id} style={{ marginBottom: 8 }}>
                  <TaskItem
                    task={task}
                    index={index}
                    onEdit={handleEditTask}
                    onDelete={handleDeleteTask}
                    editingIndex={editingIndex}
                    setEditingIndex={setEditingIndex}
                  />
                </div>
              ))}
            </List>
          </Box>
        </Paper>
      </Paper>
    </div>
  );
};

export default TaskList;
