import { useState, useEffect } from "react";
import {
  Typography,
  Paper,
  Box,
  List,
  ListItem,
  ListItemText,
  Divider,
} from "@mui/material";
import { useParams } from "react-router-dom";
import { fetchReportDetail } from "../../../api";

const ReportDetail = () => {
  const { reportId } = useParams();
  const [report, setReport] = useState(null);

  useEffect(() => {
    const load = async () => {
      try {
        const res = await fetchReportDetail(reportId);
        setReport(res.data);
      } catch (err) {
        console.error("", err);
      }
    };
    load();
  }, [reportId]);

  if (!report) return <Typography>...</Typography>;

  return (
    <Paper sx={{ padding: 3 }}>
      <Typography variant="h5" gutterBottom>
        
      </Typography>
      <Typography variant="subtitle1">
        : {new Date(report.date).toLocaleString()}
      </Typography>
      <Typography variant="subtitle1">
        : {report.placename || report.placeid}
      </Typography>
      <Typography variant="subtitle1">
        : {report.staffname || report.staffid}
      </Typography>
      <Typography variant="subtitle1">
        : {report.status}
      </Typography>
      <Typography variant="subtitle1">
        : {report.remarks || ""}
      </Typography>

      <Box mt={3}>
        <Typography variant="h6"></Typography>
        <List>
          {report.items && report.items.map((item, index) => (
            <Box key={index}>
              <ListItem alignItems="flex-start">
                <ListItemText
                  primary={` ${index + 1}`}
                  secondary={
                    <>
                      <div>ID: {item.itemid}</div>
                      {item.photourl ? (
                        <img
                          src={item.photourl}
                          alt={`-${index + 1}`}
                          style={{ marginTop: 8, maxWidth: 200, borderRadius: 4 }}
                        />
                      ) : (
                        <div style={{ color: "#999" }}></div>
                      )}
                    </>
                  }
                />
              </ListItem>
              <Divider />
            </Box>
          ))}
        </List>
      </Box>
    </Paper>
  );
};

export default ReportDetail;
