import React, { useState } from "react";
import { Typography, Button, TextField, Box } from "@mui/material";
import "./styles/SendPage.css";
import { updateReportRemarks } from "../../api"; //  

const SendPage = ({ reportId, onSubmit }) => {
  const [remarks, setRemarks] = useState("");
  const [submitted, setSubmitted] = useState(false);

  const handleSubmit = async () => {
    try {
      await updateReportRemarks(reportId, remarks);
      setSubmitted(true);
      console.log("");
      onSubmit(); //  
    } catch (err) {
      console.error("", err);
    }
  };

  return (
    <Box className="send-page-container">
      <Typography variant="h5" className="send-title"></Typography>
      {!submitted ? (
        <>
          <TextField
            fullWidth
            multiline
            rows={4}
            variant="outlined"
            label=""
            value={remarks}
            onChange={(e) => setRemarks(e.target.value)}
            className="send-textfield"
          />
          <Button onClick={handleSubmit} color="primary" variant="contained" className="send-button">
            
          </Button>
        </>
      ) : (
        <Typography variant="h6" className="send-confirmation"></Typography>
      )}
    </Box>
  );
};

export default SendPage;
